@font-face {
  font-family: 'Circee'; /*a name to be used later*/
  font-weight: normal;
  src: url('fonts/Circe/CRC55.otf'); /*URL to font*/
}
@font-face {
  font-family: 'Circee'; /*a name to be used later*/
  font-weight: bold;
  src: url('fonts/Circe/Circe-Bold.otf'); /*URL to font*/
}
*{
  font-family: Circee;
  margin: 0;
}
.nav-bar{
  display: flex;
  align-items: center;
  gap: 2rem;
  position: fixed;
  top: 0;
  background-color: rgba(255, 255, 255, 0.678);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  height: 3rem;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, .08235294117647059);
  z-index: 1000;
  box-shadow: 0 5px 8px rgba(0, 0, 0, .08235294117647059);
}

.navicons {
  position: fixed;
  width: 2.5vw;
  top: 65vh;
  left: 1vw;
  z-index: 3;
  display: flex;
  flex-direction: column;

}
.navicons img {
  margin-bottom: 0.5vw;
  width: 100%;
  transition: all 0.2s ease-in-out;
  opacity: 50%;
  cursor: pointer;
}

.logo{
  flex: 0.98;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 0.5rem;
}

.logo img{
  width: 2.5rem;
  transition: all 0.2s ease-in-out;
}

.logo img:hover {
  /* transform: scale(1.1); */
  filter: drop-shadow(2px 4px 2px black);
  transition: all 0.3s ease-in-out;
}

.link {
  opacity: 80%;
  height: 1.25rem;
  width: 1.25rem;
  transition: all 0.2s ease-in-out;
  z-index: 4;
  /*box-shadow: 10px 10px 10px #00000030;*/
}
.link:hover {
  opacity: 100%;
  transform: scale(1.15);
  transform: translate(0, -5px);
  transition: all 0.3s ease-in-out;
  cursor: pointer;

}

.link-image{
  width: 100%;
}

/*hero*/
.hero{
  height: calc(100vh);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-bottom: 5vh;
}

.info{
  width: 50%;
  min-width: 50rem;
  margin-top: -12rem;
  margin-left: -10rem;
  color: #2a2a2a;
}

.name-title{
  font-size: 6rem;
  padding: 0;
  margin: 0;
  line-height: 5rem;
  text-align: left;
  color: #363636;
  margin-bottom: 0.5rem;
  /* border: 1px solid green; */
  text-wrap: nowrap;
  /* width: 3.5ch; */
  overflow: hidden;
}

.blink{
  opacity: 0.6;
  margin: 0;
  padding: 0;
  margin-bottom: -1.25rem;
  height: 2.5rem;
  width: 4rem;
}

.name-grey{
  color: #c7c7c7;
  font-weight: lighter;
}

.title-tags{
  font-weight: lighter;
  font-size: 5.5rem;
}
.titles{
  width: 100%;
}
.titles > *{
  margin-top: 0.5rem;
  margin-bottom: 0.75rem;
  display: flex;
  height: 2rem;
  justify-content: flex-start;
  align-items: center;
  gap: 1.25rem;
  align-items: center;
  padding: 0;
  font-size: 1.25rem;
  font-weight: bold;
  margin: 0;
}

.titles img{
  width: 1.75rem;
}
.main-green{
 /* color: #3fb66c; */
 color: #46dd91;
}

.info-paragraph{
  margin-top: 0.5rem;
  line-height: 1.5;
  padding: 0;
  color:#868181;
  font-size: 1.25rem;
  font-weight: lighter;
}

.sectiontitle hr{
  height: 1px;
  width: 60%;
  opacity: 30%;

}
.sectiontitle{
  margin: 0 auto;
  margin-top: 2.5vw;
  margin-bottom: 2.5vw;

  display: flex;
  justify-content: center;
  align-items: center;
}
.sectiontitle h2{
  width: auto!important;
  line-height: 0%;
  font-size: 2rem;
  text-wrap: nowrap;

}
.sectiontitle img{
  display: inline-block;
  margin-right: 1vw;
  width: 1.5vw;

  margin-left: 0.5rem;
}

/* hero art work*/
.mahad-container{
  position: absolute;
  height: 40rem;
  width: auto;
  left: 67%;
  bottom: 15%;

}

.mahad-container2{
  position: relative;
  height: 100%;
}
.mahad-1{
 height: 100%;
}

.bucket{
  position: absolute;
  top: 70%;
  left: 0;
  width: 9rem;
}

.paint-drip, .paint-drip2{
  height: 1rem;
  transform: scale(1.1);
  animation-name: drip;


  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  position: absolute;

}

.paint-drip{
  left: 15%;
  animation-delay: 3s;
  top: 330px;
  animation-duration: 5s;
}

.paint-drip2{
  left: 17%;
  animation-delay: 7.5s;
  top: 330px;
  animation-duration: 3s;
}

@keyframes drip {
  0%   {top: 330px;height: 0.75rem}
  20% {top: 485px; height: 1.75rem}
  21% {top: 490px; height: 0rem}
  100% {top: 490px; height: 0rem}
}
.drip-container{
  height: 20vh;
  width: 100%;
  margin-top: -15vh;
  background: rgb(246,246,246);
  background: linear-gradient(180deg, rgba(246,246,246,1) 0%, rgba(255,255,255,1) 100%);
}

/*tablet*/
@media only screen and (max-width: 1000px){

  .hero{
    justify-content: flex-start;
    align-items: center;
  }
  .info{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 0;
    margin-top: 10rem;
    margin-left: 0;
    padding: 1rem;
    color: #2a2a2a;
  }

  .navicons{
    width: 3rem;
    top: 80vh;
  }

  .info-paragraph{
    width: 90%;
    min-height: 150px;

  }

  .name-title, .title-tags, .name-grey{
    font-size: 4rem;
  }

  .titles *{
    font-size: 1rem;
    justify-content: center;
    gap: 1%;
  }

  .bucket{
    top: 78%;
    right: 2vw;
    width: 11rem;
  }
  /* hero art work*/
.mahad-container{
  position: static;
  height: 40rem;
  width: auto;
  left: 67%;
  bottom: 15%;

}
.mahad-1{
 height: 100%;

}

.bucket{
  position: absolute;
  top: 70%;
  left: 0;
  width: 9rem;
}
}

/*mobile*/
@media only screen and (max-width: 600px) {
  /* html{
    overflow-x: hidden;
  } */
  body{
    max-width: 100%;
    margin: 0;
    padding: 0;
    /* overflow-x: hidden; */
  }

  .navicons{
    display: none;
  }

  .link{
    width: 1rem;
    height: 1rem;
  }
  .nav-bar{
    width: 100vw;
    gap: 1rem;
    justify-content: flex-end;
  }
  
  .logo{
    flex: 1;

  }

  .hero{
    width: 100vw;
    height: calc(100vh - 3rem);
    margin-top: 3rem;
    display: flex;

  }

  .info{
    width: 90%;
    max-width: 100%;
    height: 30%;
    margin: 0;
    min-width: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    color: #2a2a2a;
  }

  .info-paragraph{
    width: 100%;
  }
  .main-green{
    display: none!important;
  }

  .name-title{
    line-height: 100%;
    margin-bottom: 0!important;
  }

  .name-title, .name-grey, .title-tags{
    width: 100%;
    font-size: 10vw;
  }

  .titles > div p{
    text-wrap: nowrap;
  }
  
  .titles *{

    color: #3fb66c;
    margin-top: 0.5rem;
    margin-bottom: 0.75rem;
    display: flex;
    height: 2rem;
    justify-content: center;
    align-items: center;
    align-items: center;
    padding: 0;
    font-size: 3.65vw!important;
    font-weight: bold;
    margin: 0;
    position: relative;
  }

  .test{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 10vh;
    z-index: 999;
  }


  .blink{
    width: 10vw;
  }

  .sectiontitle{
    margin-top: 1rem;
    margin-bottom: 1rem;
  }

  .sectiontitle img{
    width: 1.5rem;
  }

  .sectiontitle h2{
    font-size: 1.5rem;
  }

  .sectiontitle hr{
    margin: 0;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }

  .mahad-container{
    height: 70%;
    overflow: hidden;
  }

  .bucket{
    display: none;
  }

  .paint-drip, .paint-drip2{
    display: none;
  }
}