.experience-container{
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}
.work-experience{
    position: relative;
    display: flex;
    width: 70%;
    align-items: flex-start;
    justify-content: center;
    margin: 0 auto;
    display: flex;
    /* border: 1px solid green; */
    margin-bottom: 20px;
    /* border-bottom: 1px solid #ccc; */
    border-left: 1px solid #ccc;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 20px;
    gap: 0.5rem;
}

.experience-info{
    /* border: 1px solid; */
    width: 100%;
    display: flex;
    flex-direction: column;
}

.experience-company{
    color: #3fb66c;
    font-size: 1.5rem;
    text-wrap: nowrap;
}

.experience-location{
    opacity: 0.5;
    margin-bottom: 0.25rem;
}

.experience-title{
    text-wrap: nowrap;
}

.experience-year{
    font-style: normal;
    opacity: 0.5;
    position: absolute;
    right: 101%;
    top: 2.5%;
    width: auto;
    text-wrap: nowrap;

}
.experience-image{
    max-height: 20vh;
    object-fit: contain;
    display: none;
}

.experience-description{
    /* display: none; */
    margin-top: 0.5rem;
    padding: 1rem;
    /* padding-left: 0; */
    color: white;
    background-color: rgba(0, 0, 0, .6);
    border-radius: 2px;
    filter: drop-shadow(2px 10px 5px #00000030);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    width: 60%;
    margin: auto;
    font-weight: lighter;
    font-size: 0.95vw;
}

.arrow-button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.5em;
    transform: rotate(0deg);
    transition: transform 0.3s ease;
}

.arrow-button.expanded {
    transform: rotate(90deg);
}

@media only screen and (max-width: 1000px){
    .work-experience{
        width: 100%;
        flex-direction: column;
    }
}

@media only screen and (max-width: 600px){
    .experience-container{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    .work-experience{
        flex-direction: column!important;
        padding: 0;
        border: none;
    }

    .experience-info{
        width: 100%;
        align-items: flex-start!important;
    }

    .experience-year{
        position: static;
        align-self: flex-start;
    }

    .experience-title{
        text-wrap: wrap;
    }

    .experience-image{
        width: 100%;
        max-height: none;
    }

    .experience-description{
        font-size:1rem;
        margin: none;
        width: 90%;
    }
}
