.footer{

}
.get-in-touch{
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    position: relative;
    /* border: 1px solid green; */

}

.mahad-footer{
    position: absolute;
    top: 60%;
    left: -15%;
    height: 20.5vw;
    object-fit: cover;
    object-position: top;
    width: 140%;
    opacity: 0.5;
}

.get-in-touch-title{
    font-size: 5rem;
    font-weight: 900!important;
    background-color: #ffffff;
}

h2{
    color: #2e2e2e
}

@media only screen and (max-width: 1000px){
    .get-in-touch-title, .get-in-touch-title *{
        font-size: 8vw;
    }

    .mahad-footer{
        top: 70%;
    }
}

@media only screen and (max-width: 600px){
    .footer{
        gap: 0.5rem;
        padding-bottom: 0.5rem;
    }

    small{
        line-height: auto;
    }

    hr{
        margin-top: 6vh;
    }
    .get-in-touch{
        width: 80%;
    }
    .get-in-touch-title, .get-in-touch-title *{
        font-size: 8vw;
    }

    .get-in-touch p{
        font-size: 1rem!important;
    }

    .mahad-footer{
        top: 60%;
        left: 65%;
        height: 38.5vw;
        width: 85%;
    }
}