.about{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    margin: 0 auto;
    margin-top: 5vh;
}

.about-title{
    text-align: center;
    font-size: 2vw;
    opacity: 80%;
    font-weight: 600;
}

.about-title-mobile{
    display: none;
}

.about-content-container{
    display: flex; 
    gap: 0.5rem;
    width: 80%;
    margin-bottom: 2vh;
}

.headshot-container{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.headshot{
    max-height: 35vh;
    /* position: absolute;
    top: -30px;
    left: -30px; */
    z-index: 99;
    filter: drop-shadow(2px 10px 5px #00000030);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
}
.headshot-2{
    width: 100%;
    filter: blur(5px);
}
.about-info-container{
    padding: 1rem;
    width: 80%;
    font-size: 1.2rem;
    font-weight: lighter;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.about-info{
    min-width: 400px;
    background-color: #00000080;
    padding: 0.75rem;
    color: white;
    filter: drop-shadow(2px 10px 5px #00000030);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
}

.experience-container{
    width: 80%;
}

.experience{
    width: 100%;
}
@media only screen and (max-width: 1000px){
    .about-content-container{
        width: 85%;
        flex-wrap: wrap;
    }

    .about-info{
        font-size: 2vw;
        min-width: 40vw;
    }

    .about-info-container{
        max-width: 45%;
    }

    .headshot-container{
        max-width: 45%;
    }

    .experience-container{
        width: 100%;
    }
}

@media only screen and (max-width: 600px){
    .about{
        /* margin-top: 5vh; */
        width: 90%;
    }

    .about-title{
        display: none;
    }
    .about-title-mobile{
        display: block;
        text-align: center;
        font-size: 1.5rem;
        margin-bottom: 1rem;
    }

    .headshot{
        height: 100%;
        max-height: none;
        width: 100%;
    }

    .about-content-container{
        width: 90%;
        gap: 0;
        flex-direction: column;
        align-items: center;
    }

    .headshot-container{
        max-width: 80%;
    }

    .about-info-container{
        width: 100%;
        max-width: none;

    }

    .about-info{
        font-size: 1rem;
        min-width: auto;
    }

    .info-paragraph{
        min-height: 0!important;
        font-size: 1rem;
    }


}